.pop-close {
    position: absolute;
    top: 3.5%;
    right: 4%;
    width: 65px;
    aspect-ratio: 1;
    background-image: url('../../img/global/close_navy_active_btn_@2x.png');
    background-size: contain;
}

.pop-close:hover:active, .pop-close:focus-visible {
    opacity: 1;
}

.manuf-cont-container .pop-close {
    background-image: url('../../img/global/close_white_active_btn_@2x.png');
}

.pop-close.bkg-color-white {
    background-image: url('../../img/global/close_white_btn_@2x.png');
}

.pop-close.bkg-color-navy {
    background-image: url('../../img/global/close_navy_btn_@2x.png');
}

@media screen and (aspect-ratio: 1194 / 834) {
    .pop-close {
        width: 53px;
    }
}